.header-tools {
  display: flex;

  &:only-child {
    flex: 1;
  }
}

.header-tools__items {
  display: flex;
  align-items: center;
  justify-content: flex-end;
}

.header-tools__item {
  margin-left: 30px;

  flex: 0 0 auto;

  @include break-min($md) {
    margin-left: 20px;
  }

  &:first-child {
    margin-left: 0;
  }
}

a.header-tools__button {
  @include button_ghost;
  @include button_small;
  color: $color-white;
  font-size: inherit;

  .no-hero &,
  .layout-header--scrolled & {
    color: $color-tools-icons;

    &:hover {
      color: $color-white;
    }
  }

  &.header-tools__button--external {
    display: flex;
    justify-content: center;

    svg {
      fill: #fff;
      margin-left: 5px;
      width: 14px;
    }
  }
}

.header-tools__item--language-switcher {
  display: none;

  @include break-min($headerNavShow) {
    display: block;
  }
}

.header-tools__item--cta {
  display: none;

  @include break-min($headerNavShow) {
    display: block;
  }
}

.header-tools__item--menu-toggle {
  @include break-min($headerNavShow) {
    display: none;
  }
}

.header-tools__nav-toggle {
  appearance: none;
  background: transparent;
  border: 0;
  color: $color-tools-icons;
  cursor: pointer;
  font-family: $font-body;
  font-weight: $fw-bold;
  font-size: 14px;
  margin: 0;
  outline: none;
  padding: 0;

  display: flex;
  align-items: center;

  svg {
    width: 32px;
    height: 32px;
    margin-left: $grid-gap / 2;

    display: block;
  }

  .no-hero &,
  .layout-header--scrolled & {
    color: $color-tools-icons;
  }
}
